<template>
  <div id="add-product">
    <h2></h2>
    <el-divider></el-divider>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="">
        <div class="block">
          <el-input placeholder="" v-model="p.name" clearable></el-input>
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="p.des_sel" placeholder="">
          <el-option v-for=" i in des" :key="i.id" :label="i.name" :value="i.id"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="">
        <el-input-number v-model="p.adult_price" :min="0" :max="100000" label></el-input-number>
      </el-form-item>
      <el-form-item label="">
        <el-input-number v-model="p.child_price" :min="0" :max="100000" label></el-input-number>
      </el-form-item>
      <el-form-item label="">
        <el-input-number v-model="p.days" :min="0" :max="100" label></el-input-number>
      </el-form-item>
    </el-form>
    <h3></h3>

    <h4>()</h4>
    <el-input type="textarea" :rows="8" placeholder="" v-model="p.standard"></el-input>

    <h4>()</h4>
    <el-input type="textarea" :rows="8" placeholder="" v-model="p.remark"></el-input>

    <h4>()</h4>
    <el-input type="textarea" :rows="8" placeholder="" v-model="p.chara"></el-input>

    <h4>()</h4>
    <el-input type="textarea" :rows="8" placeholder="" v-model="p.price"></el-input>
    <br />
    <br />
    <div style="text-align: center">
      <el-button type="primary" plain round @click="onsubmit(this.p)"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "AddProduct",
  data() {
    return {
      des: [],
      p: {
        name: "",
        des_sel: "",
        adult_price: "",
        child_price: "",
        days: "",
        standard: "",
        remark: "",
        chara: "",
        price: '',
      }
      
    };
  },
  methods: {
    async getDestinations() {
      try {
        //const response = await fetch('https://jsonplaceholder.typicode.com/users')
        const response = await fetch("http://localhost:8081/destination");
        const data = await response.json();
        this.des = data;
        //this.employees = data
        //alert(JSON.stringify(data));
      } catch (error) {
        alert(error);
      }
    },
    async addProduct(p) {
        try {
            const response = await fetch('http://localhost:8081/add_product', {
            method: 'POST',
            body: JSON.stringify(p),
            headers: { 'Content-type': 'application/json; charset=UTF-8' },
            })
            const data = await response.json()
            //this.employees = [...this.employees, data]
        } catch (error) {
            console.error(error)
        }
    },
    onsubmit() {
        alert("");

    }
  },
  mounted() {
    this.getDestinations();
  }
};
</script>

<style scoped>
</style>


