      },

      rules1: {
        name: [{ required: true, message: "", trigger: "blur" }],
        firm: [{ required: true, message: "", trigger: "change" }],
        fee: [{ required: true, message: "", trigger: "blur" }],
        ratio: [{ required: true, message: "", trigger: "blur" }],
        credit: [{ required: true, message: "", trigger: "blur" }],
        period: [{ required: true, message: "", trigger: "blur" }],
        client: [{ required: true, message: "", trigger: "change" }]
      },
      addClientInfo: {
        name: "",
        firm: "",
        fee: "",
        ratio: "",
        credit: "",
        period: "",
        client: "",
        principal: "",
        tel: ""
      },
      formLabelWidth: "120px",
      addClientValid: false,
      currentRow: null,
      productToSelect: null
    };
  },
  methods: {
    test() {
      alert(this.departures[0].startDate);
    },
    dialogDeparture(d) {
      this.transportVisibility = true;
      this.currentDeparture = d;
    },
    handleClientChange(val) {
      this.clientToSelect = val;
    },
    handleClientChange2(val) {
      this.productToSelect = val;
    },
    handleClientChange3(val) {
      this.transportToSelect = val;
    },
    selectClient() {
      if (this.clientToSelect === null) {
        return;
      }
      this.clientName = this.clientToSelect.name;
      this.clientId = this.clientToSelect.id;
      this.outerVisible = false;
    },
    selectProduct() {
      if (this.productToSelect === null) {
        return;
      }
      this.proName = this.productToSelect.name;
      this.productId = this.productToSelect.id;
      this.formInline.day = this.productToSelect.days;
      this.formInline.night = this.productToSelect.days - 1;
      this.productVisible = false;
    },
    selectTransport(d) {
      if (this.transportToSelect === null) {
        return;
      }
      this.currentDeparture.transport_id = this.transportToSelect.transport_id;
      this.currentDeparture.firm = this.transportToSelect.firm;
      this.currentDeparture.class = this.transportToSelect.class;
      this.currentDeparture.start = this.transportToSelect.start;
      this.currentDeparture.end = this.transportToSelect.end;
      this.currentDeparture.startTime = this.transportToSelect.startTime;
      this.currentDeparture.endTime = this.transportToSelect.endTime;
      this.transportVisibility = false;
    },
    doFilter() {
      /*if (this.tableDataName == "") {
        this.$message.warning("");
        return;
      }*/
      this.tableDataEnd = [];
      //,
      this.filterTableDataEnd = [];
      this.tableDataBegin.forEach((value, index) => {
        if (value.name) {
          if (value.name.indexOf(this.tableDataName) >= 0) {
            this.filterTableDataEnd.push(value);
          }
        }
      });
      //
      this.currentPage = 1;
      this.totalItems = this.filterTableDataEnd.length;
      //,
      this.currentChangePage(this.filterTableDataEnd);
      //
      this.flag = true;
    },
    doFilter2() {
      /*if (this.tableDataName == "") {
        this.$message.warning("");
        return;
      }*/
      this.productEnd = [];
      //,
      this.filterTableDataEnd2 = [];
      this.productBegin.forEach((value, index) => {
        if (value.name) {
          if (value.name.indexOf(this.productName) >= 0) {
            this.filterTableDataEnd2.push(value);
          }
        }
      });
      //
      this.currentPage2 = 1;
      this.totalItems2 = this.filterTableDataEnd2.length;
      //,
      this.currentChangePage2(this.filterTableDataEnd2);
      //
      this.flag2 = true;
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.pageSize = val;
      this.handleCurrentChange(1);
      this.doFilter();
    },
    handleSizeChange2(val) {
      console.log(` ${val} `);
      this.pageSize2 = val;
      this.handleCurrentChange2(1);
      this.doFilter2();
    },
    handleSizeChange3(val) {
      console.log(` ${val} `);
      this.pageSize3 = val;
      this.handleCurrentChange3(1);
    },

    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.currentPage = val;
      //
      if (!this.flag) {
        this.currentChangePage(this.tableDataBegin);
        console.log(this.tableDataEnd);
      } else {
        this.currentChangePage(this.filterTableDataEnd);
        console.log(this.tableDataEnd);
      }
    }, //

    handleCurrentChange2(val) {
      console.log(`: ${val}`);
      this.currentPage2 = val;
      //
      if (!this.flag2) {
        this.currentChangePage2(this.productBegin);
      } else {
        this.currentChangePage2(this.filterTableDataEnd2);
      }
    },

    handleCurrentChange3(val) {
      console.log(`: ${val}`);
      this.currentPage3 = val;
      //
      this.currentChangePage3(this.transportMed);
    },

    currentChangePage(list) {
      let from = (this.currentPage - 1) * this.pageSize;
      let to = this.currentPage * this.pageSize;
      this.tableDataEnd = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.tableDataEnd.push(list[from]);
        }
      }
    },
    currentChangePage2(list) {
      let from = (this.currentPage2 - 1) * this.pageSize2;
      let to = this.currentPage2 * this.pageSize2;
      this.productEnd = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.productEnd.push(list[from]);
        }
      }
    },
    currentChangePage3(list) {
      let from = (this.currentPage3 - 1) * this.pageSize3;
      let to = this.currentPage3 * this.pageSize3;
      this.transportEnd = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.transportEnd.push(list[from]);
        }
      }
    },
    postClient(password) {
      axios
        .post("http://localhost:8081/add_client", {
          userId: sessionStorage.getItem("id"),
          password: password,
          client: this.addClientInfo
        })
        .then(response => {
          if (response.data === "ok") {
            this.$message({
              type: "success",
              message: ""
            });
          } else {
            if (response.data === "password") {
              this.$message({
                message: "",
                type: "warning"
              });
              return;
            }
            this.$message({
              message: "",
              type: "warning"
            });
          }
        });
    },
    onSubmit() {
      if (this.clientName === null || this.clientName === "") {
        this.$message({
          type: "error",
          message: ""
        });
        return;
      }
      if (this.proName === null || this.proName === "") {
        this.$message({
          type: "error",
          message: ""
        });
        return;
      }
      if (this.deDate === null || this.deDate === "") {
        this.$message({
          type: "error",
          message: ""
        });
        return;
      }
      if (this.reDate === null || this.reDate === "") {
        this.$message({
          type: "error",
          message: ""
        });
        return;
      }
      let flag = false;
      this.departures.forEach(e => {
        if (e.firm === null || e.firm === "") {
          this.$message({
            type: "error",
            message: ""
          });
          flag = true;
          return;
        }
        if (e.startDate === null || e.startDate === "") {
          this.$message({
            type: "error",
            message: ""
          });
          flag = true;
          return;
        }
        if (e.endDate === null || e.endDate === "") {
          this.$message({
            type: "error",
            message: ""
          });
          flag = true;
          return;
        }
      });
      if (flag) {
        return;
      }
      this.backs.forEach(e => {
        if (e.firm === null || e.firm === "") {
          this.$message({
            type: "error",
            message: ""
          });
          flag = true;
          return;
        }
        if (e.startDate === null || e.startDate === "") {
          this.$message({
            type: "error",
            message: ""
          });
          flag = true;
          return;
        }
        if (e.endDate === null || e.endDate === "") {
          this.$message({
            type: "error",
            message: ""
          });
          flag = true;
          return;
        }
      });
      if (flag) {
        return;
      }
      if (this.tableData.length === 0) {
        this.$message({
          type: "error",
          message: ""
        });
        flag = true;
        return;
      }

      this.tableData.forEach(e => {
        if (e.item === null || e.item === "") {
          this.$message({
            type: "error",
            message: ""
          });
          flag = true;
          return;
        }
        if (e.unit === null || e.unit === "") {
          this.$message({
            type: "error",
            message: ""
          });
          flag = true;
          return;
        }
        if (e.number === null || e.number === "") {
          this.$message({
            type: "error",
            message: ""
          });
          flag = true;
          return;
        }
      });
      if (flag) {
        return;
      }
      this.submit_confirm();

      console.log("submit!");
      this.submit("ruleForm1");
    },
    submit_confirm() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputType: "password",
        inputPattern: /.......*/,
        //inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: "6"
      }).then(({ value }) => {
        this.post(value);
      });
    },
    post(password) {
      axios
        .post("http://localhost:8081/validate", {
          id: sessionStorage.getItem("id"),
          password: password
        })
        .then(response => {
          if (response.status === 200) {
            let ds = [];
            this.departures.forEach(e => {
              let i = {
                transportId: e.transport_id,
                startDate: e.startDate,
                endDate: e.endDate,
                remark: e.remark
              };
              ds.push(i);
            });
            let bs = [];
            this.backs.forEach(e => {
              let i = {
                transportId: e.transport_id,
                startDate: e.startDate,
                endDate: e.endDate,
                remark: e.remark
              };
              bs.push(i);
            });
            let send = {
              client: this.clientId,
              product: this.productId,
              days: this.formInline.day,
              nights: this.formInline.night,
              deDate: this.deDate,
              reDate: this.reDate,
              departures: ds,
              backs: bs,
              info: this.tableData,
              beizhu: this.beizhu,
              employee: sessionStorage.getItem("id")
            };
            alert(JSON.stringify(send));
            axios.post("http://localhost:8081/add_team", send);
          }
        });
    },
    submit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          //alert("submit!");
          return true;
        } else {
          //alert("error submit!!");
          this.addClientValid = false;
          return false;
        }
      });
    },
    addClient() {
      this.addClientValid = true;
      this.submit("ruleForm1");
      if (!this.addClientValid) {
        return;
      }
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputType: "password",
        inputPattern: /.......*/,
        //inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: "6"
      }).then(({ value }) => {
        this.postClient(value);
      });
    },

    add() {
      const lastId = this.departures[this.departures.length - 1].id;
      let departure = {
        id: lastId + 1,
        transport_id: "",
        firm: "",
        class: "",
        start: "",
        end: "",
        startTime: "",
        endTime: "",
        startDate: "",
        endDate: "",
        remark: ""
      };
      this.departures = [...this.departures, departure];
    },
    del(id) {
      this.departures = this.departures.filter(de => de.id !== id);
    },
    add2() {
      const lastId = this.backs[this.backs.length - 1].id;
      let back = {
        id: lastId + 1,
        transport_id: "",
        firm: "",
        class: "",
        start: "",
        end: "",
        startTime: "",
        endTime: "",
        startDate: "",
        endDate: "",
        remark: ""
      };
      this.backs = [...this.backs, back];
    },
    del2(id) {
      this.backs = this.backs.filter(de => de.id !== id);
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 2) {
          sums[index] = "";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
        } else {
          sums[index] = null;
        }
      });
      sums[3] = "/";
      return sums;
    },
    async getEmployees() {
      try {
        //const response = await fetch('https://jsonplaceholder.typicode.com/users')
        const response = await fetch("http://localhost:8081/transport");
        const data = await response.json();
        //this.employees = data
        //alert(JSON.stringify(data));
      } catch (error) {
        alert(error);
      }
    },
    async getFirms() {
      try {
        //const response = await fetch('https://jsonplaceholder.typicode.com/users')
        const response = await fetch("http://localhost:8081/firm");
        const data = await response.json();
        //this.employees = data
        //alert(JSON.stringify(data));
        this.form2.firm = data;
      } catch (error) {
        alert(error);
      }
    },
    getClients() {
      this.tableDataName = "";
      this.tableDataEnd = [];
      this.totalItems = this.tableDataBegin.length;
      if (this.totalItems > this.pageSize) {
        for (let index = 0; index < this.pageSize; index++) {
          this.tableDataEnd.push(this.tableDataBegin[index]);
        }
      } else {
        this.tableDataEnd = this.tableDataBegin;
      }
    },
    getProducts() {
      this.productName = "";
      this.productEnd = [];
      this.totalItems2 = this.productBegin.length;
      if (this.totalItems2 > this.pageSize2) {
        for (let index = 0; index < this.pageSize2; index++) {
          this.productEnd.push(this.productBegin[index]);
        }
      } else {
        this.productEnd = this.productBegin;
      }
    },
    getTransport() {
      this.transportEnd = [];
      this.totalItems3 = this.transportMed.length;
      if (this.totalItems3 > this.pageSize3) {
        for (let index = 0; index < this.pageSize3; index++) {
          this.transportEnd.push(this.transportMed[index]);
        }
      } else {
        this.transportEnd = this.transportMed;
      }
    },
    getAllClients() {
      axios.get("http://localhost:8081/get_clients").then(response => {
        this.tableDataBegin = response.data;
      });
    },
    getAllProducts() {
      axios.get("http://localhost:8081/get_products").then(response => {
        this.productBegin = response.data;
      });
      let a = {
        name: "",
        days: 2,
        adult_price: 30,
        child_price: 20
      };
      this.productBegin = [...this.productBegin, a];
    },
    getAllTransport() {
      this.transportBegin = [];
      this.getAllPlanes();
      this.getAllTrains();
    },
    getAllTrains() {
      axios.get("http://localhost:8081/get_trains").then(response => {
        response.data.forEach(e => {
          let a = {
            transport_id: e.id,
            firm: "",
            class: e.number,
            start: e.start,
            end: e.end,
            startTime: e.deTime,
            endTime: e.atime
          };
          this.transportBegin.push(a);
        });
        this.transportMed = this.transportBegin;
      });
    },
    getAllPlanes() {
      axios.get("http://localhost:8081/get_flights").then(response => {
        response.data.forEach(e => {
          let a = {
            transport_id: e.id,
            firm: e.airline,
            class: e.number,
            start: e.start,
            end: e.end,
            startTime: e.startTime,
            endTime: e.endTime
          };
          this.transportBegin.push(a);
        });
        this.transportMed = this.transportBegin;
      });
    },
    filterTransport() {
      let s = this.search.start;
      this.transportMed = this.transportBegin.filter(data => {
        return (
          data.start.indexOf(s) > -1 && data.end.indexOf(this.search.end) > -1
        );
      });
      this.getTransport();
    }
  },

  watch: {
    tableData: {
      handler: function(val, oldVal) {
        this.tableData.map((v, i) => {
          if (v.unit !== null && v.number !== null) {
            v.subtotal = v.unit * v.number;
          }
        });
        if (
          this.tableData.length > 0 &&
          this.tableData[this.tableData.length - 1].item !== ""
        ) {
          let total = {
            item: "",
            unit: "/",
            number: "0",
            subtotal: "0"
          };
        }
      },
      deep: true
    },
    search: {
      handler: function(val, oldVal) {
        this.filterTransport();
      },
      deep: true
    }
  },
  components: {
    AddLineBasic,
    AddClient
  },
  mounted() {
    this.getEmployees();
    this.getAllClients();
    this.getAllProducts();
    this.getAllTransport();
    this.transportMed = this.transportBegin;
    //this.getFirms();
  }
};
</script>

<style scoped>
.el-dialog-div {
  height: 40vh;
  overflow: auto;
}
.submit {
  position: relative;
  top: 20px;
}
</style>



