<template>
  <div id="add-client">
      

    <el-button type="text" @click="outerVisible = true"> Dialog</el-button>
    <el-dialog title=" Dialog" :visible.sync="outerVisible">
      <el-dialog append-to-body title="" :visible.sync="innerVisible">
      <el-form :model="form2">
        <el-form-item label="" >
          <el-input v-model="form2.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-select v-model="form2.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
      <div slot="footer" class="dialog-footer">
        <el-button @click="outerVisible = false"> </el-button>
        <el-button type="primary" @click="innerVisible = true"> Dialog</el-button>
      </div>
      
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'add-client',
  data() {
    return {
      outerVisible: false,
      innerVisible: false,
      form2: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      }
    };
  }
};
</script>


