<template>
  <div id="self-info">
    <h2></h2>
    <el-divider></el-divider>
    <h3></h3>
    <div>: {{username1}}</div>
    <div>: {{name1}}</div>
    <div>{{role}}</div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "selfInfo",
  data() {
    return {
      username1: "",
      name1: "",
      role: '',
    };
  },
  method: {
    async getUser() {
        alert('bb')
      try {
        let user = {
          "id": sessionStorage.getItem('id')
        }
        const response = await fetch(
          "http://localhost:8081/user",
          {
            method: "POST",
            body: JSON.stringify(user),
            headers: { "Content-type": "application/json; charset=UTF-8" }
          }
        );
        const data = await response.json();
        alert(data)
        this.name1 = data.name;
      } catch (error) {
        console.error(error);
      }
    },
  },
  mounted() {
      //this.getUser()
      axios.post("http://localhost:8081/user", {
          id: sessionStorage.getItem('id')
      })
      .then(response => {
          //alert(JSON.stringify(response.data.username));
          this.name1 = response.data.name
          this.username1 = response.data.username
          let r = response.data.role
          this.role = r === 1? "" :
          2? "":
          3? "":
          4? "":
          5? "":""

      })
  }
};
</script>

<style scoped>
</style>


